import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Button, Group, Div, FormLayout, Select, FormLayoutGroup, Radio, Avatar, PanelHeader, HeaderButton, platform, IOS } from '@vkontakte/vkui';
import './Persik.css';
import connect from '@vkontakte/vk-connect';
import '@vkontakte/vkui/dist/vkui.css';
import Icon24Qr from '@vkontakte/icons/dist/24/qr';
import SocialLinks from './SocialLinks';
import questions from '../test/test.json'
import axios from 'axios';
const osname = platform();

class Home extends React.Component {
		constructor(props){
			super(props);
			this.state = {
					form1: '',
					form2: '',
					form3: '',
					form4: '',
					form5: '',
					qrData: null,
					qrType: null,
					submit: false,
					win: null,
					args: window.location.href,
					posts: null,
			};

			this.handleInputChange = this.handleInputChange.bind(this);
			this.handleSubmit = this.handleSubmit.bind(this);
		}

		handleInputChange(e) {
	    const { name, value } = e.currentTarget;
	    this.setState({ [name]: value });
	  }

		handleSubmit(event) {
			console.log(this.state);
			var score = 0;
			var iterator = 1;
			questions.forEach((q) => {
				if (this.state["form" + iterator] === q.author)
					score++
				iterator++;
			})
			if (score > 2){
				this.setState({submit : true, win : true})
			} else {
				this.setState({submit : true, win : false})
			}
		}

		getQrData = () => {
			connect.subscribe((e) => {
				switch (e.detail.type) {
					case 'VKWebAppOpenQRResult':
						this.setState({ qrData: e.detail.data.qr_data });
						break;
					default:
						console.log(e.detail.type);
				}
			});
			connect.send('VKWebAppOpenQR');
			var where_id = this.state.args.split("where_id=")[1];

		  fetch('https://vkhack123.chickenkiller.com/api/get_location', {
		    method: 'POST',
		    body: JSON.stringify({
		      jsonrpc: '2.0',
					headers: {'Access-Control-Allow-Origin':'https://vkhack123.chickenkiller.com/'},
					data: {
						where: where_id,
						qr: this.state.qrData,
					},
		      id: 0,
		    }),
		  })
			.then(response => {
				console.log(response.json())
				this.setState({posts: response.data})
			})
			.catch(error => {
				console.log(error)
			})
		}


		render(){
				const fetchedUser = this.props.fetchedUser;
				const player = this.props.player;
				const id = this.props.id;
				const go = this.props.go;
				return (
				<Panel id={id}>
						<PanelHeader
							left={<HeaderButton onClick={this.getQrData}>
								{osname === IOS ? <Icon24Qr/> : <Icon24Qr/>}
							</HeaderButton>}
						>
							Главная страница
						</PanelHeader>
						{fetchedUser &&
						<Group title="User Data Fetched with VK Connect">
								<ListItem
								before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
								description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
								>
								{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
								</ListItem>
						</Group>}

						<Group title="Navigation Example">
								<Div>
										{this.state.posts}
								</Div>
						</Group>
						<Div>
							<Button size="xl" level="2" onClick={go} data-to="exhibit">
								Show me the Exhibit, please
							</Button>
						</Div>
						<Div>
							<Button size="xl" level="2" onClick={go} data-to="events">
								Show me Events, please
							</Button>
						</Div>
						<Div>
							<Button size="xl" level="2" onClick={go} data-to="map">
								Show me Map, please
							</Button>
						</Div>
						<Div>
							<Button size="xl" level="2" onClick={go} data-to="test">
								Show me Test, please
							</Button>
						</Div>
						{player}
						<SocialLinks/>
				</Panel>
				);
		}
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
